.app_wrapper {
    width: 90%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 2fr 10fr;
    grid-template-rows: auto 1fr;
    grid-template-areas: 'header header' 'nav content';
    grid-gap: 15px;

}

.app_wrapper_content {
    grid-area: content;
    background-color: tomato;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    min-height: 100vh;
}
